<template>
  <ApolloQuery
    :query="require('../graphql/queries/getAdminProfile.gql')"
    class="row ma-sm-4"
  >
    <template v-slot="{ result: { error, data }, isLoading }">
      <!-- Loading -->
      <div
        v-if="isLoading === 1"
        class="mx-auto"
      >
        <v-progress-circular
          indeterminate
          color="primary"
        />
      </div>

      <!-- Error -->
      <AppNotification
        v-else-if="error"
        :message="error.message"
      />

      <!-- Result -->
      <template v-else-if="data">
        <v-col v-if="!data.getAdminProfile.permissions.includes('GAMES_ADMINISTRATION') && data.getAdminProfile.role !== 'SUPERUSER'">
          <v-alert
            outlined
            type="error"
          >
            Этот раздел вам недоступен!
          </v-alert>
        </v-col>

        <v-col v-else>
          <ApolloQuery :query="require('../graphql/queries/getActiveGames.gql')">
            <template v-slot="{ result: { error: gamesError, data: gamesData }, isLoading: gamesIsLoading }">
              <!-- Error -->
              <AppNotification
                v-if="gamesError"
                :message="gamesError.message"
              />

              <!-- Result -->
              <v-card
                v-else-if="gamesData || gamesIsLoading === 1"
                :loading="gamesIsLoading === 1"
                flat
                class="px-4 pb-2"
              >
                <template v-if="gamesData">
                  <v-card-title class="py-7">
                    Игры:
                  </v-card-title>
                  <v-data-table
                    :headers="headers"
                    :items="gamesData.getActiveGames"
                    :items-per-page="5"
                    no-data-text="Игры отсутствуют"
                  >
                    <template v-slot:[`item.createdAt`]="{ item }">
                      {{ item.createdAt | moment('DD.MM.YYYY, HH:mm') }}
                    </template>

                    <template v-slot:[`item.active`]="{ item }">
                      <v-simple-checkbox
                        v-model="item.active"
                        disabled
                      />
                    </template>
                  </v-data-table>
                </template>
              </v-card>

              <!-- No result -->
              <AppNotification
                v-else
                message="Нет результата :("
              />
            </template>
          </ApolloQuery>
        </v-col>
      </template>

      <!-- No result -->
      <AppNotification
        v-else
        message="Нет результата :("
      />
    </template>
  </ApolloQuery>
</template>

<script>
import AppNotification from '@/components/AppNotification.vue';

export default {
  components: {
    AppNotification,
  },
  data() {
    return {
      headers: [
        { text: 'Режим:', value: 'gameMode', class: 'white--text text-subtitle-2' },
        { text: 'Дата создания:', value: 'createdAt', class: 'white--text text-subtitle-2' },
        { text: 'Активен:', value: 'active', class: 'white--text text-subtitle-2' },
      ],
    };
  },
};
</script>
